import { service } from '.'

interface LoginData{
  username:string,
  password:string
}
const login = (data:LoginData) => service({
  url: '/login',
  method: 'post',
  data
})

const getGoodsList = () => service({
  url: '/getGoodsList',
  method: 'get'
})

const getUserList = () => service({
  url: '/getUserList',
  method: 'get'
})

const getRoleList = () => service({
  url: '/getRoleList',
  method: 'get'
})

const getAuthorityList = () => service({
  url: '/getAuthorityList',
  method: 'get'
})

export { login, getGoodsList, getUserList, getRoleList, getAuthorityList }
