import "core-js/modules/es.array.push.js";
import { reactive, defineComponent, toRefs, ref } from 'vue';
import { useRouter } from 'vue-router';
import { login } from '../request/api';
export default defineComponent({
  setup() {
    // data
    const ruleFormRef = ref();
    const router = useRouter();
    const rules = reactive({
      username: [{
        required: true,
        message: '请填写您的用户名',
        trigger: 'blur'
      }, {
        min: 3,
        max: 10,
        message: '长度应在3-10之间',
        trigger: 'blur'
      }],
      password: [{
        required: true,
        message: '请填写您的密码',
        trigger: 'blur'
      }, {
        min: 3,
        max: 10,
        message: '长度应在3-10之间',
        trigger: 'blur'
      }]
    });
    // methods
    const submitForm = async formEl => {
      if (!formEl) return;
      await formEl.validate((valid, fields) => {
        if (valid) {
          login(state.ruleForm).then(res => {
            localStorage.setItem('token', res.data.token);
            router.push('/');
          }).catch(err => {
            if (err.data.code === 200) {
              localStorage.setItem('token', err.data.token);
              router.push('/');
            }
            console.log(err);
          });
        } else {
          console.log('提交失败！', fields);
        }
      });
    };
    const resetForm = formEl => {
      if (!formEl) return;
      formEl.resetFields();
    };
    // /
    const state = reactive({
      ruleForm: {
        username: '',
        password: ''
      },
      rules,
      submitForm,
      ruleFormRef,
      resetForm
    });
    return {
      ...toRefs(state)
    };
  }
});